  border-radius: 50%;
  background-clip: padding-box;
  position: absolute;
  left: 50%;
  top: 50%;
  opacity: 0;
  transform: translate(-50%, -50%);
  animation: gel-map-animationSignal linear 2s;
  animation-iteration-count: infinite;
  display: block;
  box-shadow: inset 0 0 35px 10px #0ec6d5;
  border-color: #0ec6d5;
  content: '';
}

.map-point-animate .marker:before {
