}

.map-point-animate .marker:after {
  animation-delay: 1s;
}

@keyframes gel-map-animationSignal {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.3);
  }
  1% {
    transform: translate(-50%, -50%) scale(0.32);
    opacity: 0.1;
  }
  20% {
    opacity: 0.5;
    transform: translate(-50%, -50%) scale(0 0.45);
  }
  60% {
