.hover-to-top {
  transition: transform .4s ease-in-out, opacity .4s ease-in-out;
}

.hover-to-top:hover {
  transform: translateY(-10px);
}

.hover-change-image .hover-image, .hover-change-image .image {
  transition: all 0.3s;
}

.hover-change-image .hover-image {
  opacity: 0;
}

.hover-change-image:hover .image {
  opacity: 0 !important;
}

.hover-change-image:hover .hover-image {
  opacity: 1 !important;
}

.play-animation:before, .play-animation:after {
  animation: play-animation 1600ms ease-in-out 0ms;
  animation-iteration-count: infinite;
  width: 100%;
  height: 100%;
  content: "";
  right: 0;
  top: 0;
  position: absolute;
  border-radius: 50%;
  transition: transform 0.3s ease-in-out;
}

.play-animation:after {
  animation-delay: 800ms;
}

@keyframes play-animation {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0px #fff;
    opacity: 0.6;
  }
  50% {
    transform: scale(1.2);
    box-shadow: 0 0 0 4px #fff;
    opacity: 0.3;
  }
  100% {
    transform: scale(1.4);
    box-shadow: 0 0 0 8px #fff;
    opacity: 0;
  }
}

.hover-zoom-in {
  overflow: hidden;
}

.hover-zoom-in img, .hover-zoom-in .img, .hover-zoom-in .card-img {
  transform: scale(1);
  transition: 1s ease-in-out;
  width: 100%;
}

.hover-zoom-in:hover img, .hover-zoom-in:hover .img, .hover-zoom-in:hover .card-img {
  transform: scale(1.3);
}

.hover-opacity {
  overflow: hidden;
}

.hover-opacity img, .hover-opacity .img, .hover-opacity .card-img {
  opacity: 1;
  transition: 1s ease-in-out;
}

.hover-opacity:hover img, .hover-opacity:hover .img, .hover-opacity:hover .card-img {
  opacity: .5;
}

.hover-shine {
  position: relative;
  overflow: hidden;
}

.hover-shine::before {
  position: absolute;
  top: 0;
  left: -100%;
  z-index: 2;
  display: block;
  content: '';
  width: 50%;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  transform: skewX(-25deg);
}

.hover-shine:hover::before {
  animation: shine 1s;
}

.custom-packages {
  position: absolute;
  right: 0;
  transform: rotate(45deg);
  top: 18px;
}

.custom-packages:before {
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  border-bottom: 50px solid #0ec6d5;
  content: '';
  position: absolute;
  z-index: -1;
  top: -28px;
  right: -21px;
  left: auto;
}

